
import { defineComponent } from "vue";
import PostImageComponent from "./image.vue";
import hljs from "highlight.js";
import { Post } from "@/types";
export default defineComponent({
  components: {
    PostImageComponent
  },
  props: {
    post: {
      type: Object as () => Post,
      required: true
    }
  },
  mounted() {
    hljs.highlightAll();
  }
});
